.footer_section{
    width: 100%;
    height: auto;
    background-color:#189AB4; 
    color: #f7f7f7;
    padding: 30px 0;
    display: flex;
    border-top: 4px solid #05445E;
    border-bottom: 4px solid #05445E;
}
/* 189AB4 05445E */
.footer_right--div{
width: 50%;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.footer_logo{
width:80px;
height: 80px;
box-shadow: 0 .2rem 1rem rgba(0,0,0,0.4);
border-radius: 50%;
}
.footer-title{
font-size:1.6rem ;
opacity: 0.9;
letter-spacing: 2px;
margin: 0.5rem 0;
text-align: center;
text-transform: uppercase;
}
.footer-slogan{
font-weight: bolder;
font-size: 1.2rem;
text-transform: uppercase;
letter-spacing: 2px;
text-align: center;
padding:10px 5px;
border: 4px dashed #05445E;
}
.footer_left--div{
width: 50%;
}
.footer-contact{
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
    margin-bottom: 10px;
    width: 95%;
}
.pen-gif{
    width: 25px;
}
.contact-num{

}
.footer-address{
letter-spacing: 1px;
font-weight: bold;
font-size: 1.1rem;
width: 100%;
opacity: 0.9;
color:white;
}
.phone-div{
    display: flex;
    margin: 20px;
    margin-left: 0;
    cursor: pointer;
}
.phone-svg{
    width: 26px;
    margin: 0 20px;
    margin-left: 0;
}

.policy-container{
    display: flex;
    flex-direction: column;
    width: 250px;
    padding: 20px 40px;
    justify-content: space-evenly;
}
.policy-anchor{
display: inline;
font-size: 18px;
font-weight: 500;
color:white;
opacity: 0.9;
}

.dev-credits{
    background-color: #05445E;
    text-align: center;
    padding: 10px 0;
}

.credits-p{
    color:#f7f7f7;
    font-weight: 500;
    letter-spacing: 1px;
}

.credits-anchor{
    color:#f7f7f7;
}
@media screen and (max-width:700px) {
    .footer_section{
        flex-direction: column;
        padding: 0 30px;
    }
    .footer_right--div{
        width: 100%;
        margin: 30px 0;
        }
        .footer_left--div{
            width: 100%;
            text-align: center;
            display: flex;
            flex-direction: column;
            align-items: center;
            }
}
@media screen and (max-width:700px) {
    .footer_logo{
        width:70px;
        height: 70px;
        }
        .footer-title{
        font-size:1.3rem ;
        margin: 1rem 0;
        }
        .footer-slogan{
        font-size: 1.0rem;
        padding:6px 3px;
        }
        .footer-contact{
         font-size: 1.6rem;
        }
        .pen-gif{
            width: 25px;
        }
        .footer-address{
        font-size: 0.9rem;
        }
        .phone-div{
            margin: 15px;
        }
        .phone-svg{
            width: 20px;
        }
        .policy-container{
            display: flex;
            width: 100%;
            align-items: center;
        }
        .policy-anchor{
       margin:8px 0;
        }
}