
  *{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
  }
  
.navbar {
  display: flex;
  position: fixed;
  top:0;
  width: 100%;
  z-index: 100;
  justify-content: space-between;
  align-items: center;
  background-color: #189AB4;
  color: white;
}

.brand-title {
  font-size: 1.5rem;
  margin: .5rem;
  margin-left: 20px;
}

.navbar-links {
  height: 100%;
}

.navbar-links ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-links li {
  list-style: none;
  border-radius: 5px;
}

.navbar-links li a {
  display: block;
  text-decoration: none;
  color: white;
  padding: 1rem;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
}

.navbar-links li:hover {
  background-color:rgba(255,255,255,0.5);
}

.toggle-button {
  position: absolute;
  top: .75rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

.logo-img{
  width: 65px;
}
.home-img{
  width: 26px;
}
.home-anchor{
  margin-right: 30px;
}

@media (max-width: 800px) {
  .navbar {
      flex-direction: column;
      align-items: flex-start;
  }

  .toggle-button {
      display: flex;
      margin: 8px 0 ;
  }

  .navbar-links {
      display: none;
      width: 100%;
  }

  .navbar-links ul {
      width: 100%;
      flex-direction: column;
  }

  .navbar-links ul li {
      text-align: center;
      margin: 10px 0;
  }

  .navbar-links ul li a {
      padding: .5rem 1rem;
  }

  .navbar-links.active {
      display: flex;
  }
  .logo-img{
    width: 40px;
  }
  .brand-title {
    margin-left: 10px;
  }
}
.cancel-btn{
  font-weight: bolder;
  font-size: 1.6rem;
  margin:0;
}

